import React from 'react'
import { Button, Row, Col } from 'react-bootstrap'
import Fader from './Fader.js'
import NumberInput from './NumberInput.js'
import Util from './util.js'
class DCSound extends React.Component {
  constructor(props) {
      super(props);
      this.state={csoundLoaded:"No",seed:5181359,pathseed:518041066,wave:0,scale:6};
      this.itm = { id : 'hvsamp', type : 'fader', label : 'amp', min: 0, max: 1, step: 0.01, defval: 1 }
      this.itmrev = { id : 'hvsrev', type : 'fader', label : 'reverb', min: 0, max: 1, step: 0.01, defval: 0.6 }
      this.itmdel = { id : 'hvsdel', type : 'fader', label : 'delay', min: 0, max: 1, step: 0.01, defval: 0.1 }
      this.itmdrums = { id : 'drums', type : 'fader', label : 'drums', min: 0, max: 1, step: 0.01, defval: 0.4 }
      this.niseed = { id : 'seed', type : 'numberinput', label : 'seed', min: 0, max: 1000000000, step: 1, defval: 0 }
      this.nipseed = { id : 'pathseed', type : 'numberinput', label : 'pseed', min: 0, max: 1000000000, step: 1, defval: 0 }
      this.niwave = { id : 'wave', type : 'numberinput', label : 'wave', min: 0, max: 8, step: 1, defval: 0 }
      this.niscale = { id : 'scale', type : 'numberinput', label : 'scale', min: 0, max: 8, step: 1, defval: 0 }

  }
  componentDidMount() {
    setTimeout(() => {
      global.CsoundObj.initialize().then(() => {
      this.csound = new global.CsoundObj();
  //use this code to load a .csd file and have Csound compile and start it
      fetch('/vcohousegen_all.csd').then((response) => {
        response.arrayBuffer().then(async (buf) => {
        this.csound.writeToFS('vcohousegen_all.csd', buf);
        this.csound.compileCSD('vcohousegen_all.csd');
        this.csound.start();
        this.setState({csoundLoaded:"Yes"});
        this.niseed.changeValue(5181359);
        this.nipseed.changeValue(518041066);
        this.niwave.changeValue(0)
        this.niscale.changeValue(6);
	      this.isPlaying = false;
	      this.isStarted = false;
        })
      })
      });
    },1000);
   }

  handleStart() {
     if(!this.isPlaying) {
       if(!this.isStarted) {
          global.CsoundObj.CSOUND_AUDIO_CONTEXT.resume();
          this.started = true;
       }
       this.csound.readScore('i "trigDrums" 0 3000');
       this.csound.readScore(`i "hvs" 0.00067 3000 110 4 ${this.state.pathseed/1000000000} ${this.state.seed} ${this.state.wave} ${this.state.scale}`);
       this.isPlaying = true;
     }
  }
  handleRandom() {
       let seed = Util.randomIntM(1000000000);
       this.niseed.changeValue(seed);
       let pathseed = Util.randomIntM(1000000000);
       this.nipseed.changeValue(pathseed);
       let wave = Util.randomIntM(9);
       this.niwave.changeValue(wave)
       let scale = Util.randomIntM(9);
       this.niscale.changeValue(scale);
       console.log(seed,pathseed,wave,scale);
  }
  handlePause() {
     if(this.started) {
       this.started = false;
       global.CsoundObj.CSOUND_AUDIO_CONTEXT.suspend();
     }
  }

  handleContinue() {
     if(!this.started) {
       this.started = true;
       global.CsoundObj.CSOUND_AUDIO_CONTEXT.resume();
     }
  }

  handleStop() {
     if(this.isPlaying) {
       this.csound.readScore('i "trigOff" 0 0.1');
       this.isPlaying = false;
     }
  }

  handleUpdate(id,val) {
    this.csound.setControlChannel(id,val);
  }

  handleNI(id,value) {
    let st = {};
    st[id]=value;
    this.setState(st);
  }

  render() {
     const {
       state: { seed,pathseed,wave,scale,csoundLoaded },
     } = this
 
    return (
      <>
      <Row>
	      <Col><Button onClick={this.handleStart.bind(this)}>Start</Button></Col>
	      <Col><Button onClick={this.handleStop.bind(this)}>Stop</Button></Col>
	      <Col><Button onClick={this.handleRandom.bind(this)}>Random</Button></Col>
	      <Col><Button onClick={this.handlePause.bind(this)}>Pause</Button></Col>
	      <Col><Button onClick={this.handleContinue.bind(this)}>Continue</Button></Col>
      </Row>
      <Row>
        <Col><NumberInput vertical={false} def={this.niseed} onChange={this.handleNI.bind(this)}/></Col>
        <Col><NumberInput vertical={false} def={this.nipseed} onChange={this.handleNI.bind(this)}/></Col>
        <Col><NumberInput vertical={false} def={this.niwave} onChange={this.handleNI.bind(this)}/></Col>
        <Col><NumberInput vertical={false} def={this.niscale} onChange={this.handleNI.bind(this)}/></Col>
      </Row>
      <Row>
        <Col><Fader def={this.itm} key={this.itm.id} onChange={this.handleUpdate.bind(this)}/></Col>
        <Col><Fader def={this.itmrev} key={this.itmrev.id} onChange={this.handleUpdate.bind(this)}/></Col>
        <Col><Fader def={this.itmdel} key={this.itmdel.id} onChange={this.handleUpdate.bind(this)}/></Col>
        <Col><Fader def={this.itmdrums} key={this.itmdrums.id} onChange={this.handleUpdate.bind(this)}/></Col>
      </Row>
      <Row><Col>Csound loaded: {csoundLoaded}</Col></Row>
      </>
    );

  }
}

export default DCSound
